import { defineSupportCode } from 'cucumber';
import { Runner } from '../../lib/runner';

defineSupportCode(function ({ Before, After, runner, eventBroadcaster }) {
  let childConsoleLog;

  Before({ timeout: 3 * Runner.maxConnectTimeout }, async function () {
    try {
      await runner.waitForServerUnavailable();
    } catch (err) {
      console.log(err.message);
      eventBroadcaster.emit('abort');
    }

    childConsoleLog = runner.start();
    await runner.waitForServerAvailable();
  });

  After(async function ({ result }) {
    await runner.stop();
    result.consoleLog = await childConsoleLog;
  });
});
